html, body {
	margin: 0;
	padding: 0;
}

.container {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-image: url('../../assets/imageBg.png');
	background-size: cover;
	background-position: center;
}

.box {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 625px;
	height: 550px;
	max-width: 100%;
	max-height: 100%;
	border-radius: 32px;
	backdrop-filter: blur(25px);
}

.logo {
	display: flex;
	width: 100px;
	height: 100px;
	background-color: white;
	border-radius: 100%;
	box-shadow: 0px 4px 11px 0px rgba(0, 0, 0, 0.25);
	margin-top: 50px;
	align-items: center;
	justify-content: center;
}

.description {
	font-family: Poppins;
	font-size: 36px;
	font-weight: 700;
	line-height: 0.2;
}

.verif {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 40px;
	text-align: center;
	width: 100%;
	font-family: Poppins;
	color: white;
	font-size: 36px;
	font-weight: 600;
	line-height: 1;
}

.loader {
	width: 48px;
	height: 48px;
	border: 5px solid #FFF;
	border-bottom-color: #FB025D;
	border-radius: 50%;
	display: inline-block;
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
	margin-top: 30px;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}